.about {
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .about p {
    color: #aaa;
    margin-bottom: 2em;
  }
  
  .about a {
    display: block;
    margin-top: 15px;
    padding: 10px 15px;
  }

  @media (max-width:992px){

    .about h2 {
      font-size: 1.5em;
    }

    p{
      font-size: 1em;
    }


    
  }

  @media (max-width:576px) {

    .about h2{
      font-size: 1.2em;
    }

  }
  